.reviews {
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  padding-top: 42px;
  padding-bottom: 30px;
}

.reviews__item {
  display: flex;
  flex-direction: column;
  width: 278px;
  margin-bottom: 33px;

  &--hidden {
    display: none;
  }
}

.reviews__author {
  font-size: 18px;
  font-weight: bold;
  font-style: normal;
  line-height: 1.67;
  color: black;
  margin: 0 auto;
  margin-bottom: 13px;
  text-align: center;
}

.reviews__text {
  max-width: 278px;
  font-size: 14px;
  font-weight: normal;
  line-height: 30px;
  text-align: center;
  color: #283645;
  display: block;
  margin: 0 auto;
}

.reviews__occupation {
  display: none;
}

.reviews__toggles {
  margin: 0 auto;
}

@media (min-width: $tablet-width) {
  .reviews {
    padding-top: 0;
    padding-bottom: 19px;
    text-align: center;
  }

  .reviews__item {
    width: 620px;
    margin-bottom: 0;
    text-align: center;
  }

  .reviews__text {
    order: 1;
    max-width: 620px;
    font-size: 18px;
    margin-top: -3px;
    margin-left: 0;
    margin-bottom: 33px;
  }

  .reviews__author {
    order: 2;
    font-size: 20px;
    margin-bottom: 8px;
  }

  .reviews__occupation {
    display: block;
    order: 3;
    margin: 0 auto;
    font-size: 14px;
    color: $dark;
  }

  .reviews__toggles {
    margin-top: 41px;
  }
}

@media (min-width: $desktop-width) {
  .reviews {
    background: url("../img/icon-quotes.svg") no-repeat 50% 72px;
    padding-top: 193px;
    padding-bottom: 0;
    width: 960px;
    margin: 0 auto;
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: 213px;
      left: 8px;
      width: 22px;
      height: 41px;
      background: url("../img/icon-review-arrow-left.svg");
      background-repeat: no-repeat;
      opacity: 0.3;
    }

    &::after {
      position: absolute;
      content: "";
      top: 213px;
      right: 12px;
      width: 22px;
      height: 41px;
      background: url("../img/icon-review-arrow-right.svg");
      background-repeat: no-repeat;
      opacity: 0.3;
    }
  }

  .reviews__item {
    width: 687px;
  }

  .reviews__text {
    max-width: 687px;
    margin-top: -8px;
    margin-left: -3px;
    margin-bottom: 31px;
  }

  .reviews__author {
    font-size: 24px;
    padding-right: 6px;
    margin-bottom: 2px;
  }

  .reviews__occupation {
    font-size: 18px;
    padding-right: 6px;
  }

  .reviews__toggles {
    visibility: hidden;
  }
}
